<template><section><html><head></head><body><h1>EarthSDK 例子</h1>
<p>使用 CesiumLab 的 EarthSDK 开发时只需要在引入 vue-cesium 时通过配置项 <code v-pre="">cesiumPath</code> 指定为 EarthSDK 路径即可:</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">VueCesium</span>, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/EarthSDK/XbsjEarth/XbsjEarth.js'</span>
})

<span class="hljs-comment">// 如果是局部引入，也可以这样:</span>
<span class="hljs-keyword">import</span> { <span class="hljs-title class_">Viewer</span> } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
<span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">Viewer</span>, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/EarthSDK/XbsjEarth/XbsjEarth.js'</span>
})
</code></pre>
<p>或者是直接在 <code v-pre="">vc-viewer</code> 组件上指定 <code v-pre="">cesiumPath</code> 属性，需要<strong>注意</strong>的是如果要使用 EarthSDK API，只能用 EarthSDK 的方法加载才行，不能放入 Cesium 原生的方法中的。<code v-pre="">vc-viewer</code> 加载成功会返回 { Cesium, viewer, earth }, 通过该 <code v-pre="">earth</code> 进行相关开发即可， 如下面的例子：</p>
<h2>示例</h2>
<h3>用 EarthSDK 加载 vc-viewer 组件</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :animation="animation" :base-layer-picker="baseLayerPicker" :timeline="timeline" :cesium-path="cesiumPath" :fullscreen-button="fullscreenButton" :fullscreen-element="fullscreenElement" :info-box="infoBox" @ready="ready">
        <vc-navigation></vc-navigation>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <vc-layer-imagery ref="layerText">
          <vc-provider-imagery-tianditu map-style="cia_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>动画部件</span>
        <md-switch v-model="animation"></md-switch>
        <span>时间轴部件</span>
        <md-switch v-model="timeline"></md-switch>
        <span>基础图层拾取器</span>
        <md-switch v-model="baseLayerPicker"></md-switch>
        <span>全屏按钮</span>
        <md-switch v-model="fullscreenButton"></md-switch>
        <span>信息提示框</span>
        <md-switch v-model="infoBox"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"viewerContainer"</span>&gt;</span>
