      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"infoBox"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
