<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">animation</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">timeline</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">baseLayerPicker</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">fullscreenButton</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">infoBox</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">fullscreenElement</span>: <span class="hljs-variable language_">document</span>.<span class="hljs-property">body</span>,
        <span class="hljs-attr">tk</span>: <span class="hljs-string">'436ce7e50d27eede2f2929307e6b33c0'</span>,
        <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/EarthSDK/XbsjEarth/XbsjEarth.js'</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">vcViewer</span>.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer }</span>) =&gt;</span> {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'viewer is loaded.'</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer, earth } = cesiumInstance
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">depthTestAgainstTerrain</span> = <span class="hljs-literal">true</span>
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">enableLighting</span> = <span class="hljs-literal">true</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">fullscreenElement</span> = <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">viewerContainer</span>
        viewer.<span class="hljs-property">entities</span>.<span class="hljs-title function_">add</span>({
